@import url('https://fonts.googleapis.com/css2?family=Anek+Latin:wght@100;200;300;400;500&family=Montserrat:wght@600&display=swap');

.navbar {
    width: 100%;
    height: 100px;
    background: white;
    display: flex;
    align-items: center ;
    justify-content: space-between;
    font-family: 'Montserrat', sans-serif; 
    box-shadow: 0 2px 4px 0 rgba(119, 119, 119, 0.2);
}

.logo-container {
    font-family: 'Anek Latin';
    color:#73737e;
    font-size: 150%;
    font-weight: 100;
    font-style: normal;

    position: absolute;
    left: 5%;
    margin-left: 80px;
}

.logo-container span {
    font-weight: 500;
    cursor: pointer;
}

.links-container {
    float: right;
    color: #73737e;
    text-decoration: none;
    font-size: 90%;
    margin-right:30px;

    display: flex;
    width: 250px;
    justify-content: space-evenly;
}

.link a {
    font-size: 80%;
    text-decoration: none;
    color: #73737e;
    justify-content: space-evenly;
    float: left;
    display: block;
    text-align: center;
    text-decoration: none;
}

.link a:hover {
    text-decoration: none;
    color: rgba(14, 34, 14, 0.8);
}

.link a.active {
    text-decoration: none;
    color: #1a1b1f;    
  }

  
  .link .icon {
    display: none;
  }
  
  @media screen and (max-width: 600px) {
    .navbar a:not(:first-child) {display: none;}
    .navbar a.icon {
      float: right;
      display: block;
    }
  }
  
  @media screen and (max-width: 600px) {
    .navbar.responsive {position: relative;}
    .navbar.responsive .icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .navbar.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }